.glide {
  position: relative;
  width: 100%;
  box-sizing: border-box; }
  .glide * {
    box-sizing: inherit; }
  .glide__track {
    overflow: hidden; }
  .glide__slides {
    position: relative;
    width: 100%;
    list-style: none;
    backface-visibility: hidden;
    transform-style: preserve-3d;
    touch-action: pan-Y;
    overflow: hidden;
    padding: 0;
    white-space: nowrap;
    display: flex;
    flex-wrap: nowrap;
    will-change: transform; }
    .glide__slides--dragging {
      user-select: none; }
  .glide__slide {
    width: 100%;
    height: 100%;
    flex-shrink: 0;
    white-space: normal;
    user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent; }
    .glide__slide a {
      user-select: none;
      -webkit-user-drag: none;
      -moz-user-select: none;
      -ms-user-select: none; }
  .glide__arrows {
    -webkit-touch-callout: none;
    user-select: none; }
  .glide__bullets {
    -webkit-touch-callout: none;
    user-select: none; }
  .glide--rtl {
    direction: rtl; }

.glide__arrow {
  position: absolute;
  display: block;
  top: 50%;
  z-index: 2;
  color: white;
  text-transform: uppercase;
  padding: 9px 12px;
  background-color: transparent;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 4px;
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.1);
  text-shadow: 0 0.25em 0.5em rgba(0, 0, 0, 0.1);
  opacity: 1;
  cursor: pointer;
  transition: opacity 150ms ease, border 300ms ease-in-out;
  transform: translateY(-50%);
  line-height: 1; }
  .glide__arrow:focus {
    outline: none; }
  .glide__arrow:hover {
    border-color: white; }
  .glide__arrow--left {
    left: 2em; }
  .glide__arrow--right {
    right: 2em; }
  .glide__arrow--disabled {
    opacity: 0.33; }

.glide__bullets {
  position: absolute;
  z-index: 2;
  bottom: 2em;
  left: 50%;
  display: inline-flex;
  list-style: none;
  transform: translateX(-50%); }

.glide__bullet {
  background-color: rgba(255, 255, 255, 0.5);
  width: 9px;
  height: 9px;
  padding: 0;
  border-radius: 50%;
  border: 2px solid transparent;
  transition: all 300ms ease-in-out;
  cursor: pointer;
  line-height: 0;
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.1);
  margin: 0 0.25em; }
  .glide__bullet:focus {
    outline: none; }
  .glide__bullet:hover, .glide__bullet:focus {
    border: 2px solid white;
    background-color: rgba(255, 255, 255, 0.5); }
  .glide__bullet--active {
    background-color: white; }

.glide--swipeable {
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab; }

.glide--dragging {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing; }

html, body {
  margin: 0;
  height: 100%;
  box-sizing: border-box; }

body {
  font: verdana, sans-serif;
  background-color: #2c2a2a; }
  body h1, body h2 {
    font-family: "Sacramento", cursive; }
  body p, body article {
    font-family: "Metrophobic", sans-serif;
    line-height: 1.3pc; }

.btn {
  width: 10rem;
  background: inherit;
  border: 1px solid white;
  cursor: pointer;
  font-family: "Metrophobic", sans-serif;
  color: white;
  border-radius: 3px;
  display: block;
  padding: 0.6rem;
  transition: background-color 350ms;
  /* justify-self: flex-end; */
  align-self: center;
  z-index: 4; }

.btn:hover, .btn:active {
  background-color: #d19a2d; }

header {
  height: 100vh; }
  header .header-bg {
    background-image: linear-gradient(transparent, #2c2a2a), url(assets/img/zastawa.jpg);
    background-size: cover;
    width: 100%;
    height: 100%;
    position: absolute; }

nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-content: center;
  padding: 3rem 1rem; }
  nav .brand img {
    width: 250px;
    min-width: 250px;
    cursor: pointer; }
  nav .sociallinks {
    padding: 0;
    display: flex; }
    nav .sociallinks li:nth-child(2) {
      order: 2; }
    nav .sociallinks li {
      list-style: none;
      margin: 0 1rem; }
      nav .sociallinks li svg {
        width: 30px;
        height: 30px;
        fill: white;
        transition: fill 350ms; }
    nav .sociallinks svg:hover {
      fill: #d19a2d; }
  nav .items-right {
    display: flex; }
  nav .hamburger-container {
    margin: auto 0;
    height: 100%;
    display: flex; }
    nav .hamburger-container .hamburger {
      width: 50px;
      height: 2px;
      background: #FCFCFC;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
      margin: auto 0;
      transition: 0.5s;
      position: relative; }
  nav .hamburger:before,
  nav .hamburger:after {
    content: '';
    position: absolute;
    width: 50px;
    height: 2px;
    background: #FCFCFC;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
    transition: 0.5s; }
  nav .hamburger:before {
    top: -16px; }
  nav .hamburger:after {
    top: 16px; }
  nav .hamburger-container.active .hamburger {
    background: rgba(0, 0, 0, 0);
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0); }
  nav .hamburger-container.active .hamburger::before {
    top: 0;
    transform: rotate(140deg); }
  nav .hamburger-container.active .hamburger::after {
    top: 0;
    transform: rotate(40deg); }
  nav #toggle-nav__checkbox {
    position: absolute;
    display: none; }
  nav .cta {
    margin: 0 2rem; }

.arrow {
  cursor: pointer;
  position: absolute;
  width: 100%;
  text-align: center;
  margin: auto;
  bottom: 0; }
  .arrow * {
    width: 50px;
    height: 50px;
    z-index: 1;
    fill: white;
    margin: auto;
    transition: transform 350ms ease; }
  .arrow:hover svg {
    transform: scale(1.4); }

